import { headerSearch } from "../../plugins/ComponentsUi/HeaderSearch/HeaderSearch";
import { detectCloseElement } from "./helper";
/*==================== Header ====================*/
/**
 * @param header
 */
const vw = $(window).width();
export const header = {
	scrollActive: function () {
		let height = $("header").height();
		if ($(window).scrollTop() > height) {
			$("header").addClass("active");
		} else {
			$("header").removeClass("active");
		}
	},
	mobile: function () {
		$(".hambuger").on("click", function () {
			$(this).toggleClass("active");
			$("body").toggleClass("isOpenMenu");
		});
	},
	initVariable: function () {
		console.log("initVariable");
		const heightAdminbar = $("#wpadminbar").length ? $("#wpadminbar").outerHeight() : 0;
		const height = $("header").height();
		const heightTop = $("header").height() - $(".header-top-bar").height();
		const heightBottom = $(".header-bottom").outerHeight() + heightAdminbar;
		document.documentElement.style.setProperty("--header-height", `${height}px`);
		document.documentElement.style.setProperty("--header-height-top", `${heightTop}px`);
		document.documentElement.style.setProperty("--header-bottom", `${heightBottom}px`);
	},
	init: function () {
		headerSearch();
		header.scrollActive();
		header.mobile();
	},
};

