export const headerScroll = {
	lastScrollTop: 0,
	scrollThreshold: 100, // Minimum scroll distance to trigger
	isHidden: false,
	throttleTimeout: null,

	handleScroll: function () {
		if (this.throttleTimeout) {
			return;
		}
		const heightAdminbar = $("#wpadminbar").length ? $("#wpadminbar").outerHeight() : 0;
		const heightHeader = $("header").height();
		const heightTop = $("header").height() - $(".header-top-bar").height();
		const heightBottom = $(".header-bottom").outerHeight() + heightAdminbar;

		this.throttleTimeout = setTimeout(() => {
			const st = $(window).scrollTop();
			const scrollDiff = Math.abs(st - this.lastScrollTop);

			// Only trigger if scrolled more than threshold
			if (scrollDiff > this.scrollThreshold) {
				if (st > this.lastScrollTop && !this.isHidden && st > 200) {
					// Scrolling down
					$("header").addClass("active");
					$("body").addClass("isActiveHeader");
					document.documentElement.style.setProperty("--header-scroll", `${heightBottom}px`);
					this.isHidden = true;
				} else if (st < this.lastScrollTop && this.isHidden) {
					// Scrolling up
					$("header").removeClass("active");
					$("body").removeClass("isActiveHeader");
					document.documentElement.style.setProperty("--header-scroll", `${heightHeader}px`);
					this.isHidden = false;
				}
				this.lastScrollTop = st;
			}

			this.throttleTimeout = null;
		}, 150);
	},

	init: function () {
		$(window).on("scroll", () => this.handleScroll());
	},
};
