export const product = {
	productFilter: () => {
		const btnFilter = $(".button-filter-product");
		const panelFilter = $(".list-filter-product");
		const btnClose = $(".list-filter-product .close");
		btnFilter.on("click", function () {
			panelFilter.toggleClass("active");
		});
		btnClose.on("click", function () {
			panelFilter.toggleClass("active");
		});
	},
	initHeightPanelFilter: () => {
		const panelFilter = $(".list-filter-product");
		const totalHeight = panelFilter
			.find(".item-filter-product")
			.slice(0, 3)
			.toArray()
			.reduce((sum, el) => sum + $(el).outerHeight(), 0);
		if (totalHeight > 700) {
			panelFilter.css("max-height", `${totalHeight}px`);
		}
	},
	productToggleFilter: () => {
		$(".list-filter-product .item-filter-product .filter-title").on("click", function () {
			$(this).next().slideToggle().closest(".item-filter-product").toggleClass("close");
		});
	},
	init: () => {
		product.productFilter();
		product.initHeightPanelFilter();
		product.productToggleFilter();
		window.initHeightPanelFilter = product.initHeightPanelFilter;
	},
};
