export function showMoreButton() {
	$(document).ready(function () {
		const $rows = $(".recruitment-table tbody tr");
		const itemsPerPage = 10;
		let currentItems = itemsPerPage;

		// Initially hide all rows beyond first 10
		$rows.slice(itemsPerPage).addClass("hidden");

		$("#showMoreButton").on("click", function (e) {
			e.preventDefault();

			// Show next 10 items
			$rows.slice(currentItems, currentItems + itemsPerPage).removeClass("hidden");
			currentItems += itemsPerPage;

			// Hide button if no more items
			if (currentItems >= $rows.length) {
				$(this).hide();
			}
		});
	});
}

showMoreButton();
