export function headerSearch () {
	function closeSearch () {
		$("body").removeClass("isOpenSearch");
	}
	$(".tool-mobile .header-search").on("click", function () {
		if ($("body").hasClass("isOpenSearch")) {
			closeSearch();
		} else {
			$("body").addClass("isOpenSearch");
		}
		setTimeout(() => {
			$(".header-search-form input").focus();
		}, 400);
	});
	// $("header .header-search").on("click", function () {
	// 	// if ($("body").hasClass("isOpenSearch")) {
	// 	// 	closeSearch();
	// 	// } else {
	// 	// 	$("body").addClass("isOpenSearch");
	// 	// }
	// 	setTimeout(() => {
	// 		$(".header-search-form input").focus();
	// 	}, 400);
	// });
	$(".header-search-form .close").on("click", function () {
		closeSearch();
	});
	$(document).keyup(function (e) {
		if (e.key === "Escape") {
			closeSearch();
		}
	});
	// $(document).on("click", function (e) {
	// 	if ($("body").hasClass("isOpenSearch")) {
	// 		if (!$(e.target).closest(".header-search-form").length && !$(e.target).is(".header-search")) {
	// 			closeSearch();
	// 		}
	// 	}
	// });
}
