function scrollToActiveCategory () {
	const nodeParent = $("#menu-product");
	$(nodeParent).scrollTo("li.current-menu-item", 800);
}

function clickToolCta () {
	if (window.matchMedia("(max-width: 1023.98px)").matches) {
		$('.tool-cta .comments').on('click', function () {
			$(this).closest('.tool-cta').toggleClass('is-active');
			$('.btn-compare-trigger').addClass('is-active');
		});
		detectCloseElement('.tool-cta', '.tool-cta .comments', () => {
			$('.tool-cta').removeClass('is-active');
			$('.btn-compare-trigger').removeClass('is-active');
		});
	}
}

let resizeTimeout;
$(window).on('resize', function () {
	clearTimeout(resizeTimeout);
	resizeTimeout = setTimeout(function () {
		takeSpaceOfMenu();
		getHeightChild();
		header.initVariable();
	}, 500);
});



window.headerHeight = header.initVariable();
function handleAjaxLink () {
	$(document).on("click", "a.ajax-link", function (e) {
		e.preventDefault();
		const url = $(this).attr("href");
		const textLoading = $('html').attr('lang') === 'vi' ? 'ang ti d liu' : 'Loading data';
		$(this).parent().addClass("current").siblings().removeClass("current");
		$.ajax({
			url: url,
			method: "GET",
			beforeSend: function () {
				$(".ajax-response").html('<div class="loading-ajax">' + textLoading + '</div>');
			},
			success: function (response) {
				const newContent = $(response).find(".ajax-response").html();
				$(".ajax-response").html(newContent);
			},
			error: function (xhr, status, error) {
				console.error("Ajax request failed:", error);
			},
			complete: function () {
				$(".ajax-response .loading-ajax").remove();
				observer.observe();
			},
		});
	});
}
function tableMaterial () {
	const $cells = $(".section-material-table td");
	var correct = '<i class="fa-solid fa-circle-check"></i>';
	var incorrect = '<i class="fa-solid fa-circle-xmark"></i>';
	$cells.each(function () {
		const cellText = $(this).text().trim();
		if (cellText === "o") {
			$(this).html(correct);
		} else if (cellText === "x") {
			$(this).html(incorrect);
		}
	});
}
function closeTopBar () {
	$(".header-top-bar .close").on("click", function () {
		$(".header-top-bar").addClass("hide");
		setTimeout(() => {
			header.initVariable();
		}, 500);
	});
}
function boxNavigation () {
	$(".list-navigation ul li[class*='has-children']>a").on("click", function (e) {
		e.preventDefault();
		$(this).toggleClass("active").next().slideToggle().end().parent().siblings().find("ul").slideUp();
	});
	$(".btn-close").on("click", function () {
		$(".box-navigation").removeClass("active");
	});
	$(".btn-filter-navigation").on("click", function () {
		$(".box-navigation").toggleClass("active");
	});
}
function watchObserveInputFile () {
	console.log(" ~ watchObserveInputFile:", watchObserveInputFile)
	$('input[name="url_cv"]').on("change", function () {
		const file = this.files[0];
		console.log(" ~ file:", file)
		if (file) {
			const fileName = file.name;
			// Create/update filename display
			const fileNameDisplay = $("<span>", {
				class: "file-name text-sm text-body-text-66 mt-1 block",
				text: "File CV: " + fileName,
			});

			// Remove existing filename display if any
			$(this).closest(".form-input-file").find(".file-name").remove();
			// Add new filename display
			$(this).closest(".form-input-file").append(fileNameDisplay);
		} else {
			// If input is cleared, remove the filename display
			$(this).closest(".form-input-file").find(".file-name").remove();
		}
	});
}
function menuSpy () {
	var elm = document.querySelector("#menu-spy");
	let debounceTimer;
	let isActive = false;

	var ms = new MenuSpy(elm, {
		activeClass: "active",
		threshold: 300,
		callback: function (currentItem) {
			const nodeParent = $(".section-scrollTo-active");
			clearTimeout(debounceTimer);
			isActive = true;
			debounceTimer = setTimeout(function () {
				console.log("This is the last activation within 1 second window");
				$(nodeParent).scrollTo("li.active", 800);
				isActive = false;
			}, 1000);
		},
	});
}
function clickScrollToSection () {
	function checkIndexHashInWrap (hash) {
		const $wrap = $('.wrap-section');
		const $item = $wrap.find(`#${hash.substring(1)}`).index();
		return $item;
	}
	$('.section-menu-sticky .list-tab-sticky a').on('click', function (e) {
		e.preventDefault();
		const hash = checkIndexHashInWrap($(this).attr('href'));
		const hashUrl = checkIndexHashInWrap(window.location.hash);
		if (Number(hash) < Number(hashUrl)) {
			if (window.matchMedia("(max-width: 1023.98px)").matches) {
				$('html, body').animate({
					scrollTop: $(this.hash).offset().top - parseInt(getComputedStyle(document.documentElement).getPropertyValue('--header-height')) - 45,
				}, 'fast');
			} else {
				$('html, body').animate({
					scrollTop: $(this.hash).offset().top - parseInt(getComputedStyle(document.documentElement).getPropertyValue('--header-height')),
				}, 'fast');
			}
		} else {
			if (window.matchMedia("(max-width: 1023.98px)").matches) {
				$('html, body').animate({
					scrollTop: $(this.hash).offset().top - parseInt(getComputedStyle(document.documentElement).getPropertyValue('--header-height')) - 45,
				}, 'fast');
			} else {
				$('html, body').animate({
					scrollTop: $(this.hash).offset().top - parseInt(getComputedStyle(document.documentElement).getPropertyValue('--header-bottom')),
				}, 'fast');
			}
		}
	});
}

$(".tabs").tabslet({
	active: 2,
	animation: true,
});

function getHeightChild () {
	$(".item-var-height").each(function () {
		const height = $(this).outerHeight();
		$(this)
			.closest(".wrap-item-height")
			.css("--height-ele", height + "px");
	});
}

function playVideoBanner () {
	$(".section-home-banner .btn-stop-video").on("click", function () {
		$(".section-home-banner").removeClass("isPlayingVideo");
		// Reset Video
		$(".section-home-banner video")[0].pause();
		$(".section-home-banner video")[0].currentTime = 0;
	});
	$(".section-home-banner .btn-play").on("click", function () {
		$(".section-home-banner").addClass("isPlayingVideo");
		$(".section-home-banner video")[0].play();
	});
}

function takeSpaceOfMenu () {
	if (window.matchMedia("(max-width: 1023.98px)").matches) return;
	const $menu = $(".header-main-menu");
	const $items = $menu.find("li");
	const menuWidth = $menu.width();
	const totalItems = $items.length;

	// Calculate space between items
	const space = Math.floor((menuWidth - $items.toArray().reduce((acc, item) => acc + $(item).width(), 0)) / (totalItems - 1));
	$(".header-main-menu").css("--gap", space + "px");
	$(".header-main-menu").addClass("show-gap");
	// Apply margin to items except last one
}

