import Swiper from "swiper";
import { Autoplay, Pagination, Navigation, EffectFade, Thumbs, Controller, Scrollbar, Mousewheel } from "swiper/modules";
/**
 * @param swiperInit
 */
export function swiperInit () {
	// Swiper-Home-6
	$(".swiper-column-auto").each(function (index) {
		const $this = $(this);
		// Configuration flagsvideoSetting
		const config = {
			loop: $this.hasClass("swiper-loop"),
			touchMove: $this.hasClass("allow-touchMove") || true,
			mouseWheel: $this.hasClass("allow-mouseWheel") ? { forceToAxis: true } : false,
			autoHeight: $this.hasClass("auto-height"),
			hasVideo: $this.hasClass("auto-detect-video"),
			progressbar: $this.hasClass("progressbar"),
			time: $this.attr("data-time") || 3500,
			autoplay: $this.hasClass("autoplay"),
		};

		// Add unique identifier class
		$this.addClass(`swiper-column-auto-id-${index}`);

		// Create swiper with optimized options
		new Swiper(`.swiper-column-auto-id-${index} .swiper`, {
			modules: [Navigation, Pagination, Mousewheel, Autoplay],
			speed: 500,
			observer: true,
			observeParents: true,
			spaceBetween: 0,
			loop: config.loop,
			...(config.autoplay && {
				autoplay: {
					delay: config.time,
				},
			}),
			slidesPerView: "auto",
			pagination: {
				el: `.swiper-column-auto-id-${index} .swiper-pagination`,
				clickable: true,
				...(config.progressbar && {
					type: 'progressbar',
				}),
			},
			mousewheel: config.mouseWheel,
			allowTouchMove: config.touchMove,
			navigation: {
				prevEl: `.swiper-column-auto-id-${index} .btn-prev`,
				nextEl: `.swiper-column-auto-id-${index} .btn-next`,
			},
			watchSlidesProgress: true,
			autoHeight: config.autoHeight,
			on: {
				init: function () {
					// Check slide count and add class if necessary
					const $swiperWrapper = $this; // $this refers to the outer .swiper-column-auto element
					const classList = $swiperWrapper.attr('class').split(' ');
					const columnClass = classList.find(cls => cls.startsWith('auto-') && cls.endsWith('-column'));
					if (columnClass) {
						const expectedColumns = parseInt(columnClass.split('-')[1], 10);
						if (!isNaN(expectedColumns)) {
							const actualSlides = $swiperWrapper.find(".swiper-slide").length; // 'this' refers to the Swiper instance here
							if (actualSlides < expectedColumns) {
								$swiperWrapper.addClass('less-than-expected-slides');
							}
						}
					}
				},
				slideChange: function () {
				},
			},
		});
	});
	var SwiperHome6_Thumb = new Swiper(".section-home-6 .wrap-thumb-content .swiper", {
		slidesPerView: 6,
		spaceBetween: 10,
		loop: true,
		slideToClickedSlide: true,
		observer: true,
		observeParents: true,
		observeSlideChildren: true,
		modules: [Controller],
		breakpoints: {
			1024: {
				slidesPerView: 6,
				spaceBetween: 20,
			},
		},
		on: {
			slideChange: function () {
				window.lozad.observe();
			},
		},
	});
	var SwiperHome6_Main = new Swiper(".section-home-6 .wrap-slide-content .swiper", {
		speed: 500,
		slidesPerView: 1,
		spaceBetween: 20,
		loop: true,
		modules: [EffectFade, Controller],
		on: {
			slideChange: function () {
				window.lozad.observe();
			},
		},
	});
	var SwiperHome6_Content_Product = new Swiper(".section-home-6 .wrap-slide-product .swiper", {
		speed: 500,
		slidesPerView: 1,
		spaceBetween: 20,
		loop: true,
		modules: [EffectFade, Controller],
		effect: "fade",
		allowTouchMove: false,
	});
	SwiperHome6_Main.controller.control = [SwiperHome6_Thumb, SwiperHome6_Content_Product];
	SwiperHome6_Thumb.controller.control = SwiperHome6_Main;
	new Swiper(".section-home-banner .swiper", {
		slidesPerView: 1,
		spaceBetween: 0,
		speed: 1000,
		loop: true,
		effect: "fade",
		autoplay: {
			delay: 3500,
		},
		modules: [Pagination, Navigation, Autoplay, EffectFade],
		pagination: {
			el: ".section-home-banner .swiper-pagination",
			clickable: true,
			renderBullet: function (index, className) {
				const slide = this.slides[index];
				const title = slide.getAttribute("data-title") || `Slide ${index + 1}`;
				return `<span class="${className}">${title}</span>`;
			},
		},
		navigation: {
			nextEl: ".section-home-banner .btn-next",
			prevEl: ".section-home-banner .btn-prev",
		},
	});
	// Reuseable Swiper
	$(".swiper-cols-1 .swiper").each(function () {
		new Swiper(this, {
			slidesPerView: 1,
			spaceBetween: 20,
			loop: true,
			modules: [Navigation],
			navigation: {
				nextEl: $(this).closest(".swiper-cols-1").find(".btn-next")[0],
				prevEl: $(this).closest(".swiper-cols-1").find(".btn-prev")[0],
			},
		});
	});
	$(".swiper-cols-2 .swiper").each(function () {
		new Swiper(this, {
			slidesPerView: 1,
			spaceBetween: 20,
			loop: true,
			modules: [Navigation],
			navigation: {
				nextEl: $(this).closest(".swiper-cols-1").find(".btn-next")[0],
				prevEl: $(this).closest(".swiper-cols-1").find(".btn-prev")[0],
			},
			breakpoints: {
				768: {
					slidesPerView: 2,
					spaceBetween: 40,
				},
			},
		});
	});
	$(".swiper-cols-3 .swiper").each(function () {
		const config = {
			slidesPerView_sm: this.getAttribute("swiper-sm-slides") || 1,
			slidesPerView_md: this.getAttribute("swiper-md-slides") || 2,
			slidesPerView_lg: this.getAttribute("swiper-lg-slides") || 3,
			slidesPerView_xl: this.getAttribute("swiper-xl-slides") || 3,
			spaceBetween_sm: this.getAttribute("swiper-sm-padding") || 5,
			spaceBetween_md: this.getAttribute("swiper-md-padding") || 15,
			spaceBetween_lg: this.getAttribute("swiper-lg-padding") || 20,
			spaceBetween_xl: this.getAttribute("swiper-xl-padding") || 20,
			autoplay: this.hasAttribute("swiper-autoplay"),
			loop: this.hasAttribute("swiper-loop"),
		};
		new Swiper(this, {
			slidesPerView: config.slidesPerView_sm,
			spaceBetween: config.spaceBetween_sm,
			modules: [Navigation, Pagination, Autoplay],
			...(config.autoplay && {
				autoplay: {
					delay: 3500,
				},
			}),
			...(config.loop && {
				loop: true,
			}),
			navigation: {
				nextEl: $(this).closest(".swiper-cols-3").find(".btn-next")[0],
				prevEl: $(this).closest(".swiper-cols-3").find(".btn-prev")[0],
			},
			pagination: {
				el: $(this).closest(".swiper-cols-3").find(".swiper-pagination")[0],
				type: "progressbar",
			},
			breakpoints: {
				768: {
					slidesPerView: config.slidesPerView_md,
					spaceBetween: config.spaceBetween_md,
				},
				1024: {
					slidesPerView: config.slidesPerView_lg,
					spaceBetween: config.spaceBetween_lg,
				},
				1200: {
					slidesPerView: config.slidesPerView_xl,
					spaceBetween: config.spaceBetween_xl,
				},
			},
		});
	});
	$(".swiper-cols-4 .swiper").each(function () {
		new Swiper(this, {
			slidesPerView: 1,
			spaceBetween: 20,
			modules: [Navigation],
			navigation: {
				nextEl: $(this).closest(".swiper-cols-4").find(".btn-next")[0],
				prevEl: $(this).closest(".swiper-cols-4").find(".btn-prev")[0],
			},
			breakpoints: {
				768: {
					slidesPerView: 3,
				},
				1200: {
					slidesPerView: 4,
					spaceBetween: 40,
				},
			},
		});
	});
	new Swiper(".section-3d-printer.section-7 .wrap-slide-logo .swiper", {
		slidesPerView: 2,
		spaceBetween: 20,
		modules: [Navigation, Autoplay],
		autoplay: {
			delay: 3500,
		},
		speed: 1000,
		loop: true,
		navigation: {
			nextEl: ".section-3d-printer.section-7 .wrap-slide-logo .btn-next",
			prevEl: ".section-3d-printer.section-7 .wrap-slide-logo .btn-prev",
		},
		on: {
			init: function () {
				if (window.matchMedia("(min-width: 1200px)").matches) {
					// const numberofSlides = this.slides.length;
					// if (numberofSlides < 4) {
					// 	this.params.slidesPerView = numberofSlides;
					// 	this.el.classList.add("less-than-expected-slides");
					// }
				}
			},
		},
		breakpoints: {
			768: {
				slidesPerView: 3,
			},
			1024: {
				slidesPerView: 4,
			},
			1200: {
				slidesPerView: 4,
				spaceBetween: 32,
			},
		},
	});
	new Swiper(".section-home-2 .swiper", {
		slidesPerView: 1.2,
		spaceBetween: 20,
		modules: [Navigation, Autoplay],
		autoplay: {
			delay: 3500,
		},
		speed: 1000,
		loop: true,
		navigation: {
			nextEl: ".section-home-2 .btn-next",
			prevEl: ".section-home-2 .btn-prev",
		},
		breakpoints: {
			768: {
				slidesPerView: 1.3,
			},
			1024: {
				slidesPerView: 1.3,
			},
			1200: {
				slidesPerView: 2.53,
			},
		},
	});
	// Swiper logo
	new Swiper(".section-about-1 .swiper", {
		slidesPerView: 2,
		spaceBetween: 10,
		modules: [Navigation, Autoplay],
		autoplay: {
			delay: 3500,
		},
		loop: true,
		speed: 1000,
		navigation: {
			nextEl: ".section-about-1 .btn-next",
			prevEl: ".section-about-1 .btn-prev",
		},
		breakpoints: {
			768: {
				slidesPerView: 3,
			},
			1024: {
				slidesPerView: 4,
				spaceBetween: 32,
			},
			1200: {
				slidesPerView: 7,
				spaceBetween: 32,
			},
		},
	});
	var ThumbHistory = new Swiper(".section-about-history .wrap-slide-years .swiper", {
		slidesPerView: 3,
		spaceBetween: 10,
		speed: 300,
		centeredSlides: true,
		loop: true,
		modules: [Navigation, Thumbs],
		navigation: {
			nextEl: ".section-about-history .wrap-slide-years .btn-next",
			prevEl: ".section-about-history .wrap-slide-years .btn-prev",
		},
		breakpoints: {
			768: {
				slidesPerView: 5,
			},
			1024: {
				slidesPerView: 6,
			},
			1280: {
				slidesPerView: "auto",
			},
		},
	});
	var MainHistory = new Swiper(".section-about-history .wrap-slide-content .swiper", {
		spaceBetween: 10,
		speed: 500,
		slidesPerView: 1,
		centeredSlides: true,
		loop: true,
		autoHeight: true,
		modules: [Navigation, Thumbs],
		navigation: {
			prevEl: ".section-about-history .wrap-slide-content .btn-prev",
			nextEl: ".section-about-history .wrap-slide-content .btn-next",
		},
		thumbs: {
			swiper: ThumbHistory,
		},
	});
	new Swiper(".section-customer-detail.section-5 .swiper", {
		speed: 1000,
		spaceBetween: 15,
		slidesPerView: 1.2,
		centeredSlides: true,
		loop: true,
		autoplay: {
			delay: 3500,
		},
		modules: [Navigation, Autoplay],
		navigation: {
			prevEl: ".section-customer-detail.section-5 .wrap-button-slide-custom .btn-prev",
			nextEl: ".section-customer-detail.section-5 .wrap-button-slide-custom .btn-next",
		},
		breakpoints: {
			1024: {
				slidesPerView: 1.5,
				spaceBetween: 40,
			},
		},
	});
	// Slide
	$(".slide-printer-scrollbar .swiper").each(function () {
		new Swiper(this, {
			slidesPerView: 1,
			spaceBetween: 15,
			modules: [Scrollbar, Navigation],
			scrollbar: {
				el: $(this).closest(".slide-printer-scrollbar").find(".swiper-scrollbar")[0],
				hide: false,
			},
			navigation: {
				nextEl: $(this).closest(".slide-printer-scrollbar").find(".btn-next")[0],
				prevEl: $(this).closest(".slide-printer-scrollbar").find(".btn-prev")[0],
			},
			breakpoints: {
				768: {
					slidesPerView: 1.8,
					spaceBetween: 15,
				},
				1024: {
					slidesPerView: 1.8,
					spaceBetween: 15,
				},
			},
		});
	});

	// slide product
	var productThumbs = new Swiper(".product-thumb-image .swiper", {
		slidesPerView: 3,
		spaceBetween: 15,
		freeMode: true,
		watchSlidesProgress: true,
		autoplay: {
			delay: 1500,
		},
		speed: 300,
		breakpoints: {
			768: {
				spaceBetween: 15,
				slidesPerView: 3,
			},
			1024: {
				spaceBetween: 20,
				slidesPerView: 4,
			},
			1200: {
				spaceBetween: 20,
				slidesPerView: 5,
			},
		},
	});
	var productDetail = new Swiper(".product-main-image .swiper", {
		spaceBetween: 10,
		speed: 500,
		modules: [Thumbs, Navigation],
		slidesPerView: 1,
		rewind: true,
		thumbs: {
			swiper: productThumbs,
		},
		navigation: {
			nextEl: ".product-main-image .btn-next",
			prevEl: ".product-main-image .btn-prev",
		},
	});
	window.productDetail = productDetail;
}
